"use client";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import PostForm from "./(DashboardComponents)/post-form";
import axios from "axios";
import { useEffect, useState } from "react";

interface Comment {
  author: string;
  content: string;
}

interface Image {
  url: string;
}

interface Post {
  id: string;
  title: string;
  content: string;
  author?: {
    name: string;
    avatar?: string;
  };
  createdAt?: string;
  images?: Image[];
  likes?: number;
  comments?: Comment[];
}

export default function DashboardPage() {
  const [posts, setPosts] = useState<Post[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<null | string>(null);

  const fetchPosts = async () => {
    setIsLoading(true);
    try {
      const response = await axios.get("/api/posts");
      // Ensure we're setting an array of posts
      setPosts(
        Array.isArray(response.data)
          ? response.data
          : response.data?.posts || response.data?.data || []
      );
      setError(null);
    } catch (err) {
      console.error("Error fetching posts:", err);
      setError("Failed to load posts. Please try again later.");
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    // fetchPosts();
    setIsLoading(true);
    setPosts([
      {
        id: "1",
        title: "Exploring the Mountains",
        content: "Had an amazing time hiking through the Rockies!",
        author: {
          name: "Ava Rivers",
          avatar: "https://i.pravatar.cc/150?img=12",
        },
        createdAt: new Date().toISOString(),
        likes: 12,
        comments: [{ author: "John", content: "Looks awesome!" }],
        images: [
          { url: "https://source.unsplash.com/600x400/?mountain,hiking" },
        ],
      },
      {
        id: "2",
        title: "Cozy Workspace Setup",
        content: "Redesigned my home workspace for productivity and comfort.",
        author: {
          name: "Leo Grant",
          avatar: "https://i.pravatar.cc/150?img=18",
        },
        createdAt: new Date().toISOString(),
        likes: 45,
        comments: [
          { author: "Mia", content: "Love the vibe!" },
          { author: "Eli", content: "Where did you get that chair?" },
        ],
        images: [
          { url: "https://source.unsplash.com/600x400/?workspace,interior" },
          { url: "https://source.unsplash.com/600x400/?office,setup" },
        ],
      },
    ]);
    setIsLoading(false);
  }, []);

  const handlePostCreated = () => {
    fetchPosts(); // Refresh posts after creating a new one
  };

  const handleDeletePost = async (postId: string) => {
    try {
      await axios.delete(`/api/posts/${postId}`);
      setPosts(posts.filter((post) => post.id !== postId));
    } catch (err) {
      console.error("Error deleting post:", err);
      setError("Failed to delete post. Please try again later.");
    }
  };

  return (
    <div className="space-y-4">
      <div className="container mx-auto py-6 max-w-5xl">
        <Tabs defaultValue="feed" className="space-y-6">
          <div className="flex items-center justify-between">
            <TabsList>
              <TabsTrigger value="feed">Your Feed</TabsTrigger>
              <TabsTrigger value="create">Create Post</TabsTrigger>
            </TabsList>
          </div>

          <TabsContent value="create" className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle>Create a New Post</CardTitle>
                <CardDescription>
                  Share your thoughts, images, and more with your network
                </CardDescription>
              </CardHeader>
              <CardContent>
                <PostForm onPostCreated={handlePostCreated} />
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="feed" className="space-y-6">
            <PostList
              posts={posts}
              isLoading={isLoading}
              error={error}
              onDeletePost={handleDeletePost}
            />
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
